.films {
	font-size: 12px;
	font-weight: 500;
	line-height: 1.3;

	&__item {
		margin-bottom: 20px;
	}

	&__img:hover,
	&__img:focus {
		transform:  translateY(-50px) rotate(7deg) scale(1.1);
		
	}
	&__img {
		display: block;
		border-radius: 5px;
		max-width: 280px;
		height: 402px;
		margin-bottom: 10px;
		transition: transform 250ms var(--animation-function);
		@media screen and (min-width: 768px) {
			height: 455px;
		}
		@media screen and(min-width: 1280px) {
			height: 574px;
		}
	}
	&__title {
		text-transform: uppercase;
	}
	&__descr {
		display: flex;
		flex-wrap: wrap;
		color: #ff6b08;
		@media screen and(min-width:768px) {
			max-width: 336px;
		}
		@media screen and(min-width:1280px) {
			max-width: 394px;
		}
	}
	&__descr:first-letter {
		text-transform: uppercase;
	}

	@media screen and (min-width: 768px) {
		display: flex;
		flex-wrap: wrap;
		gap: 32px;

		&__img {
			max-width: 336px;
		}
		&__title {
			display: flex;
			flex-wrap: wrap;
			width: 336px;
		}
	}
	@media screen and (min-width: 1280px) {
		font-size: 20px;
		line-height: 1.3;
		gap: 16px;

		&__img {
			max-width: 394px;
		}
		&__title {
			width: 394px;
		}
	}
}
